<template>
  <span>
    <v-list class="mt-3">
      <v-img :aspect-ratio="5/1" src="https://images.pexels.com/photos/370799/pexels-photo-370799.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940">
        <v-toolbar flat class="transparent">
          <v-layout pa-0 column fill-height class="lightbox white--text">
            <v-spacer></v-spacer>
            <v-flex shrink>
              <v-list class="ml-2">
                <v-list-tile avatar>
                  <v-list-tile-avatar>
                    <v-img
                    src="https://randomuser.me/api/portraits/men/85.jpg"
                    ></v-img>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title class="text-white">{ Username }</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list>
            </v-flex>
          </v-layout>
        </v-toolbar>
      </v-img>
    </v-list>

    <v-list dense>
      <v-divider></v-divider>

      <v-list-tile
        v-for="item in items"
        :key="item.title"
        @click="redirect(item.link)"
      >
        <v-list-tile-action>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-tile-action>

        <v-list-tile-content>
          <v-list-tile-title>
              {{ item.title }}
          </v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>

      <router-link to="login" class="no-text-decoration">
        <v-list-tile>
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>Sair</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </router-link>

    </v-list>
  </span>
</template>
<script>
export default {
  data () {
    return {
      items: [
        {
          title: 'Home',
          icon: 'home',
          link: '/'
        },
        {
          title: 'Pesquisar Funcionario',
          icon: 'search',
          link: 'buscar'
        },
        {
          title: 'Cadastrar Funcionario',
          icon: 'assignment_ind',
          link: 'cadastrar'
        },
        {
          title: 'Ver Cadastros Incompletos',
          icon: 'assignment',
          link: 'incompletos'
        }
      ],
      right: null
    }
  },
  methods: {
    redirect (link) {
      this.$router.push(link)
    }
  }
}
</script>
<style>

</style>



